.film-card {
  display: flex;
  margin-right: 30px;
  margin-bottom: 30px;

  &:last-child {
    margin-right: 0;
    margin-bottom: 50px;

    @media screen and (min-width: 320px) {
      margin-right: 0;
      margin-bottom: 20px;

      &:last-child {
        margin-bottom: 40px;
      }
    }
  }
}

.film-image {
  width: 274px;

  @media screen and (min-width: 768px) {
    width: 293px;
  }
  @media screen and (min-width: 320px) {
    width: 280px;
  }
}

.film-title {
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  text-transform: uppercase;
  color: rgba(0, 0, 0, 1);
}

.data-list {
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: rgba(255, 107, 8, 1);
}

.data-vote {
  width: 36px;
  height: 16px;
  background: rgba(255, 107, 1, 1);
  border-radius: 5px;
}

.vote-average {
  display: flex;
  align-items: center;
  text-align: center;
  text-transform: uppercase;

  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: rgba(255, 255, 255, 1);
}
